  -webkit-align-items: center;
          align-items: center;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  padding: 10px;

  height: 25px;
  border: 1px solid lightgray;
  border-radius: 999px;
}
.header__center > input {
