}
.header__right > button {
  background-color: red;
  color: white;
  text-transform: inherit;
  font-weight: bold;
  box-shadow: none;
  padding: 10px;
  width: 90px;
}

.header__right > button:hover {
  background-color: white;
  color: red;
  box-shadow: none;
